.boton-clear{
    height: 70px;
    font-size: 1.6em;
    display: flex;
    flex: 1;
    justify-content: center;
    align-items: center;
    background-color: #ac0246;
    margin-top: 8px;
    font-weight: bold;
    color: #fff;
    border: 2px solid #fff;
    cursor: pointer;
    border-radius: 25px;

}

.boton-clear:hover{
    background-color: #ebb000;
    box-shadow: 0 0 10px rgb(0,0,0);
}