.input{
    height: 50px;
    border-radius: 50px;
    margin-bottom: 20px;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    font-weight: bold;
    font-size: 25px;
    background-color: #0a0a23;
    color: #fff;
    padding: 11px 30px 11px 11px;
    border: 2px solid #888;
    box-shadow: 0px 4px 4px 1px #fff ;
}

