.boton-contenedor{
    height: 65px;
    flex: 1 1;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: bold;
    font-size: 30px;
    background-color: #1b1b32;
    color: #fff;
    border-radius: 25px;
    border: 2px solid #fff;
    margin: 4px;
    cursor: pointer;
    user-select: none;
}

.boton-contenedor:hover{
    background-color: #5a01a7;
    box-shadow: 0 0 10px rgb(0,0,0);
}

.operador{
    background-color: #00471b;
}

.operador:hover{
    background-color: #ff6600;
    box-shadow: 0 0 10px rgb(0,0,0);
}