.titulo{
  width: 100%;
  padding-bottom: 50px;
  height: 50px;
  display: flex;
  font-family: 'Barriecito', cursive;
  font-size: 1.5rem;
  align-items: center;
  justify-content: center;
  color: #000;   
  text-shadow: -3px 3px #ffffff, -3px -3px #ffffff, 3px -3px #ffffff, 3px 3px #f3f3f2, 6px 6px #6ac7c2;
  border-radius: 25px;
  margin-bottom: 30px;
    
}

